.textbox-container {
    margin-bottom: 20px;
  }
  
  .textbox-label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
  }
  
  .textbox-input {
    width: 90%;
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: border-color 0.3s;
  }
  
  .textbox-input:focus {
    outline: none;
    border-color: #007bff;
  }
  
  .textbox-input.error {
    border-color: #dc3545;
  }
  
  .error-message {
    color: #dc3545;
    margin-top: 5px;
    font-size: 14px;
  }